/**
 * Language codes with a mapping to a corresponding default locale.
 */
const languageDefaultLocale: Record<string, Locale | undefined> = {
    bn: "bn-BD",
    da: "da-DK",
    de: "de-DE",
    el: "el-GR",
    en: "en-US",
    es: "es-ES",
    fi: "fi-FI",
    fil: "fil-PH",
    fr: "fr-FR",
    gu: "gu-IN",
    hi: "hi-IN",
    id: "id-ID",
    it: "it-IT",
    ja: "ja-JP",
    kn: "kn-IN",
    ko: "ko-KR",
    ml: "ml-IN",
    mr: "mr-IN",
    ms: "ms-MY",
    nb: "nb-NO",
    nl: "nl-NL",
    pl: "pl-PL",
    pt: "pt-BR",
    ro: "ro-RO",
    ru: "ru-RU",
    sv: "sv-SE",
    ta: "ta-IN",
    te: "te-IN",
    th: "th-TH",
    tr: "tr-TR",
    ur: "ur-PK",
    vi: "vi-VN",
    zh: "zh-Hans",
};

/**
 * Some locale synonyms that require mapping.
 */
const synonyms: Record<string, Locale | undefined> = {
    "zh-TW": "zh-Hant",
    "zh-CN": "zh-Hans",
};

type Locale = keyof typeof allStrings;
const isLocaleString = (value: string): value is Locale => value in allStrings;

// Default locale in case a user locale cannot be found.
const defaultLocale = "en-US" satisfies Locale;

/**
 * Gets browser locale and maps it to the closest locale that has a translation.
 */
function getSupportedLocale() {
    const locale = getPlatformInfo().locale;
    if (isLocaleString(locale)) return locale;

    const synonym = synonyms[locale];
    if (synonym && synonym in allStrings) return synonym;

    const langCode = locale.split("-")[0];
    // Safety: "in" operator ensures that langCode is in allStrings, i.e. is of Locale type.
    if (langCode && langCode in allStrings) return langCode as Locale;
    // Safety: "in" operator ensures that langCode is in languageDefaultLocale.
    if (langCode && langCode in languageDefaultLocale) return languageDefaultLocale[langCode]!;

    return defaultLocale;
}

/**
 * The most closest locale with a translation.
 */
export const supportedLocale = getSupportedLocale();

/**
 * Returns localized string by its ID.
 */
export function localizedString<ID extends keyof LocalizableStrings>(stringId: ID): LocalizableStrings[ID] {
    return allStrings[supportedLocale][stringId];
}
